import { Fragment } from 'react';
import classNames from 'classnames';
import { Transition } from 'react-transition-group';
import Link from 'components/Link';
import { Button } from 'components/Button';
import DecoderText from 'components/DecoderText';
import Divider from 'components/Divider';
import Image from 'components/Image';
import Section from 'components/Section';
import ProfileImg from 'assets/profile-edgar.jpg';
import ProfileImgLarge from 'assets/profile-large-edgar.jpg';
import ProfileImgPlaceholder from 'assets/profile-edgar-placeholder.jpg';
import { reflow } from 'utils/transition';
import { media } from 'utils/style';
import { ReactComponent as KatakanaProfile } from 'assets/katakana-profile.svg';
import Heading from 'components/Heading';
import Text from 'components/Text';
import './Profile.css';

const ProfileText = ({ status, titleId }) => (
  <Fragment>
    <Heading
      className={classNames('profile__title', `profile__title--${status}`)}
      level={3}
      id={titleId}
    >
      <DecoderText text="Howdy!" start={status !== 'exited'} delay={500} />
    </Heading>
    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      Im Edgar, a Web Developer with 2+ years of experience designing and developing user
      interfaces, testing, debugging, and training staff within software technologies.
      Proven ability optimizing web functionalities that improve data retrieval and
      workflow efficiency. Currently I live in Houston, working as a CEO at{' '}
      <Link href="https://standardsapps.com/">Standards Apps</Link>
    </Text>
    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      Unexpected events took me out of my comfort zone as a lawyer, turning the hobby of
      programming into my passion/jobs. After all, they will both deal with conditions,
      but unlike people, CPUs meet them. Know{' '}
      <Link href="/projects/skills"> My Skills</Link>
    </Text>

    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      That man to whom I pay homage on the banks of the Seine has been decontextualized to
      justify authoritarianism, feeling indispensable will make you fail, even if you are{' '}
      <Link href="https://en.wikipedia.org/wiki/Sim%C3%B3n_Bol%C3%ADvar">
        El Libertador
      </Link>
    </Text>
  </Fragment>
);

const Profile = ({ id, visible, sectionRef }) => {
  const titleId = `${id}-title`;

  return (
    <Section
      className="profile"
      as="section"
      id={id}
      ref={sectionRef}
      aria-labelledby={titleId}
      tabIndex={-1}
    >
      <Transition in={visible} timeout={0} onEnter={reflow}>
        {status => (
          <div className="profile__content">
            <div className="profile__column">
              <ProfileText status={status} titleId={titleId} />
              <Button
                secondary
                className={classNames('profile__button', `profile__button--${status}`)}
                href="/contact"
                icon="send"
              >
                Send me a message
              </Button>
            </div>
            <div className="profile__column">
              <div className="profile__tag" aria-hidden>
                <Divider
                  notchWidth="100%"
                  notchHeight="2px"
                  collapsed={status !== 'entered'}
                  collapseDelay={1000}
                />
                <div
                  className={classNames(
                    'profile__tag-text',
                    `profile__tag-text--${status}`
                  )}
                >
                  About Me
                </div>
              </div>
              <div className="profile__image-wrapper">
                <Image
                  reveal
                  delay={100}
                  placeholder={ProfileImgPlaceholder}
                  srcSet={`${ProfileImg} 480w, ${ProfileImgLarge} 960w`}
                  sizes={`(max-width: ${media.mobile}px) 100vw, 480px`}
                  alt="Me standing in front of the Torii on Miyajima, an island off the coast of Hiroshima in Japan"
                />
                <KatakanaProfile
                  className={classNames('profile__svg', `profile__svg--${status}`)}
                />
              </div>
            </div>
          </div>
        )}
      </Transition>
    </Section>
  );
};

export default Profile;
